import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import './Navbar12.css'
import { 
  Link
} from "react-router-dom";

export class NavBar extends Component {
  

  render() {
    return (
      <div>
        <nav className="navbar fixed-top navbar-expand-lg navbar-dark" style={{backgroundColor: '#12112a'}}>
            <div className="container-fluid">
                <Link className="navbar-brand" to="/" style={{fontFamily: 'Playfair Display'}}>NewsMonkey</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">

                        <li id='p1' className="nav-item"><Link className="nav-link" aria-current="page" to="/">Home</Link></li>
                        <li id='p1' className="nav-item"><Link className="nav-link" to="/business">Business</Link></li>
                        <li id='p1' className="nav-item"><Link className="nav-link" to="/entertainment">Entertainment</Link></li>
                        <li id='p1' className="nav-item"><Link className="nav-link" to="/general">General</Link></li>
                        <li id='p1' className="nav-item"><Link className="nav-link" to="/health">Health</Link></li>
                        <li id='p1' className="nav-item"><Link className="nav-link" to="/science">Science</Link></li>
                        <li id='p1' className="nav-item"><Link className="nav-link" to="/sports">Sports</Link></li>
                        <li id='p1' className="nav-item"><Link className="nav-link" to="/technology">Technology</Link></li>

                    </ul>
                </div>
            </div>
        </nav>
      </div>
    )
  }
}

export default NavBar
